.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;


}
.dialogsItems {
    padding: 10px;


}
.dialogsItems a {
    text-decoration: none;
    color: white;

}

.dialogsItems a:active {
    color: gold;

}
.dialogsItems img {
    width: 50px;
}
.messages {
    padding: 10px;

}

.messages .message {
    padding-top: 10px;

}

.box textarea {
    border-radius: 10px;
}
